import React, { useState } from "react"; 
import styles from "./Question.module.css" 

function Question(){
        const [formData, setFormData] = useState({name: "", phone: "", email: "", question: ""});
        const [errors, setErrors] = useState({});
    
        const validate = () => {
          const newErrors = {};
          let message = ""
          if (!formData.name.trim()) {
            newErrors.name = " " + "\n";
            message += newErrors.name + " "
          }
      
          const phoneRegex = /^\+7\d{10}$|^8\d{10}$/;
          if (!formData.phone) {
            newErrors.phone = "  " + "\n";
            message += newErrors.phone + " "
          } else if (!phoneRegex.test(formData.phone)) {
            newErrors.phone = "    (: +71234567890  81234567890)" + "\n";
            message += newErrors.phone + " "
          }
    
          const emailRegex = /^\S+@\S+\.\S+$/; 
          if(!emailRegex.test(formData.email) & formData.email != ""){
            newErrors.email = "    email@mail.ru" + "\n"
            message += newErrors.email + " "
          }

          if(!formData.question){
            newErrors.question = " " + "\n"
            message += newErrors.question + " "
          }
    
          setErrors(newErrors);
      
          return message;
        };
      
        const handleSubmit = (e) => {
          e.preventDefault();
          const result = validate()
          if (!result) {
            let email = ""
            if(!formData.email){
                email = " "
            }else{
                email = formData.email
            }
            alert("  ! \n: " + formData.name +
                "\n : " + formData.phone +
                "\n  : " + email +
                "\n: " + formData.question);
            console.log(" :", formData);
          } else{
            alert(result)
          }
        };
      
        const handleChange = (e) => {
          const { name, value } = e.target;
          setFormData((prevData) => ({ ...prevData, [name]: value}));
        };
    
    return(
        <section>
            <div className={styles.question}>
                <div className={styles.title}>
                    <h1> </h1>
                </div>
                <div className={styles.appeal}>
                    <h3>   ?    !</h3>
                    <h3>  ,         .</h3>
                </div>
                <div className={styles.reg_form}>
                    <form id="feedback" onSubmit={handleSubmit}>
                        <div><input name="name" id="name" type="text" placeholder="  " value={formData.name} onChange={handleChange}></input></div>
                        <div><input name="phone" id="phone" type="text" placeholder="   " value={formData.phone} onChange={handleChange}></input></div>
                        <div><input name="email" id="email" type="text" placeholder="  e-mail ()" value={formData.email} onChange={handleChange}></input></div>
                        <div><textarea name="question" id="question" type="text" placeholder="  ?" value={formData.question} onChange={handleChange}></textarea></div>
                        <div className={styles.submit}><button form="feedback" type="submit"></button></div>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default Question